import React from 'react';

interface StablecoinHolding {
  symbol: string;
  amount: number;
  value: number;
  apy: number;
  dailyYield: number;
}

interface DashboardProps {
  stablecoinHoldings: StablecoinHolding[];
  totalBitcoinAccrued: number;
  bitcoinPrice: number;
  totalDeposited: number;
  totalYieldGenerated: number;
  onDeposit: () => void;
  onWithdraw: () => void;
}

export default function Dashboard({
  stablecoinHoldings,
  totalBitcoinAccrued,
  bitcoinPrice,
  totalDeposited,
  totalYieldGenerated,
  onDeposit,
  onWithdraw
}: DashboardProps) {
  const totalDailyYield = stablecoinHoldings.reduce((sum, holding) => sum + holding.dailyYield, 0);
  const bitcoinValue = totalBitcoinAccrued * bitcoinPrice;

  return (
    <div className="space-y-6">
      {/* Key Metrics */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <div className="bg-card p-6 rounded-lg border">
          <div className="text-sm font-medium text-muted-foreground">Total Deposited</div>
          <div className="text-2xl font-bold">${totalDeposited.toLocaleString()}</div>
        </div>
        <div className="bg-card p-6 rounded-lg border">
          <div className="text-sm font-medium text-muted-foreground">Bitcoin Accumulated</div>
          <div className="text-2xl font-bold">{totalBitcoinAccrued.toFixed(8)} BTC</div>
          <div className="text-sm text-muted-foreground">â‰ˆ ${bitcoinValue.toLocaleString()}</div>
        </div>
        <div className="bg-card p-6 rounded-lg border">
          <div className="text-sm font-medium text-muted-foreground">Total Yield Generated</div>
          <div className="text-2xl font-bold">${totalYieldGenerated.toFixed(2)}</div>
        </div>
        <div className="bg-card p-6 rounded-lg border">
          <div className="text-sm font-medium text-muted-foreground">Daily Yield</div>
          <div className="text-2xl font-bold">${totalDailyYield.toFixed(2)}</div>
        </div>
      </div>

      {/* Stablecoin Holdings */}
      <div className="bg-card p-6 rounded-lg border">
        <h3 className="text-lg font-semibold mb-4">Your Stablecoin Holdings</h3>
        <div className="space-y-4">
          {stablecoinHoldings.map((holding) => (
            <div key={holding.symbol} className="flex items-center justify-between p-4 bg-muted rounded-lg">
              <div className="flex items-center space-x-3">
                <div className="w-10 h-10 bg-primary rounded-full flex items-center justify-center text-primary-foreground font-bold">
                  {holding.symbol.charAt(0)}
                </div>
                <div>
                  <div className="font-medium">{holding.symbol}</div>
                  <div className="text-sm text-muted-foreground">
                    {holding.amount.toLocaleString()} {holding.symbol}
                  </div>
                </div>
              </div>
              <div className="text-right">
                <div className="font-medium">${holding.value.toLocaleString()}</div>
                <div className="text-sm text-muted-foreground">
                  {holding.apy}% APY â€¢ ${holding.dailyYield.toFixed(2)}/day
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Action Buttons */}
      <div className="flex gap-4 justify-center">
        <button
          onClick={onDeposit}
          className="bg-primary text-primary-foreground px-6 py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors"
        >
          Deposit Stablecoins
        </button>
        <button
          onClick={onWithdraw}
          className="bg-secondary text-secondary-foreground px-6 py-3 rounded-lg font-medium hover:bg-secondary/90 transition-colors"
        >
          Withdraw
        </button>
      </div>
    </div>
  );
} 